.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background: url(../images/star.svg) no-repeat center;
  }

  @for $index from 1 to 5 {
    &--#{$index} &__star:nth-child(-n+#{$index}) {
      background-image: url(./../images/star-active.svg);
    }
  }
}
